.garden{
    color: rgb(216, 111, 111);
    font-family: "historial";
}

.parallax {
  padding: 0;
  perspective: 1px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.parallax__layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.parallax__layer--base {
  transform: translateZ(0);
}
.parallax__layer--back {
  margin: 0;
  padding: 0;
  min-width: 100%;
  transform: translateZ(-1px) scale(2.01);
}